import { nanoid } from "nanoid";

const getRandomInRange = (min, max) =>
  Math.floor(Math.random() * (max - min + 1)) + min;

export const firstNames = [
  "–ò–æ—Å–∏—Ñ",
  "–ù–∏–∫–æ–ª–∞–π",
  "–°–∞–º—É–∏–ª",
  "–ù–∏–∫–æ–ª–∞–π",
  "–í–ª–∞–¥–∏–º–∏—Ä",
  "–°–µ—Ä–≥–µ–π",
  "–í–∞—Å–∏–ª–∏–π",
  "–ú–∞–∫—Å–∏–º–∏–ª–∏–∞–Ω",
  "–ú–∏—Ö–∞–∏–ª",
  "–ò–≥–æ—Ä—å",
  "–ú–∏—Ö–∞–∏–ª",
  "–ê–ª–µ–∫—Å–∞–Ω–¥—Ä",
  "–õ–µ–≤",
  "–ê–Ω–Ω–∞",
  "–ê–Ω—Ç–æ–Ω",
  "–°–µ—Ä–≥–µ–π"
];
export const lastNames = [
  "–ë—Ä–æ–¥—Å–∫–∏–π",
  "–ì—É–º–∏–ª—ë–≤",
  "–ú–∞—Ä—à–∞–∫",
  "–†–µ—Ä–∏—Ö",
  "–î–∞–ª—å",
  "–î–æ–≤–ª–∞—Ç–æ–≤",
  "–®—É–∫—à–∏–Ω",
  "–í–æ–ª–æ—à–∏–Ω",
  "–ó–æ—â–µ–Ω–∫–æ",
  "–°–µ–≤–µ—Ä—è–Ω–∏–Ω",
  "–õ–µ—Ä–º–æ–Ω—Ç–æ–≤",
  "–ü—É—à–∫–∏–Ω",
  "–¢–æ–ª—Å—Ç–æ–π",
  "–ê—Ö–º–∞—Ç–æ–≤–∞",
  "–ß–µ—Ö–æ–≤",
  "–ï—Å–µ–Ω–∏–Ω"
];
export const createListEmployees = () => {
  const employees = [];
  firstNames.forEach((name, i) => {
    const employee = {
      id: nanoid(),
      firstName: name,
      lastName: lastNames[i],
      age: getRandomInRange(30, 100)
    };
    employees.push(employee);
  });
  return employees;
};
